/* src/Calculadora.css */

.calculadora {
  width: 350px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s;
}

.calculator-container.dark .calculadora {
  background-color: #3a3a3a;
  color: #ffffff;
}

.display input {
  height: 40px;
  font-size: 24px;
}

.result {
  font-size: 24px;
  font-weight: bold;
  margin-top: 10px;
}

.theme-toggle {
  position: absolute;
  bottom: 10px;
  left: 10px;
  background-color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.theme-toggle:hover {
  background-color: #ddd;
}

.calculator-container.dark .theme-toggle {
  background-color: #666;
  color: #fff;
}

.calculator-container.dark .theme-toggle:hover {
  background-color: #555;
}
